import React from "react"
import CoffeeComponent from "./components/CoffeeComponent";
import IncrementComponent from "./components/IncrementComponent";


const App = () => {

    return (

        <div>
            <CoffeeComponent name={"Blue Java"} roast={"Lightly"} />
            <CoffeeComponent name={"Geisha"} roast={"Roasted"} />
            <CoffeeComponent name={"Tropical"} roast={"Strongly"} />

            <IncrementComponent />
        </div>
    )
}


export default App
